/****************************************
  Functions
****************************************/
const sortByString = (a, b, sort) => {
  if (a < b) {
    return sort === 'asc' ? -1 : 1;
  }
  if (a > b) {
    return sort === 'asc' ? 1 : -1;
  }
  return 0;
}
const sortByNumber = (a, b, sort) => {
  const numberA = Number(a);
  const numberB = Number(b);
  return sort === 'asc' ? numberB - numberA : numberA - numberB;
}

const sortItems = (list, accessor, sort) => {
  if (!accessor && !sort) return [...list];

  switch (accessor) {

    case "usedBy":
      return list.sort((a, b) => sortByString(a.info.usedBy.toUpperCase(), b.info.usedBy.toUpperCase(), sort));

    case "details": // Post Type
      return list.sort((a, b) => sortByString(a.name.toUpperCase(), b.name.toUpperCase(), sort));

    case "table": // Table
      return list.sort((a, b) => sortByString(a.table.toUpperCase(), b.table.toUpperCase(), sort));

    case "size": // Table or Option
      return list.sort((a, b) => {
        if (a.hasOwnProperty('size')) {
          return sortByNumber(a.size, b.size, sort);
        } else if (a.hasOwnProperty('option_value_length')) {
          return sortByNumber(a.option_value_length, b.option_value_length, sort);
        }
        return 0;
      });

    case "name": // Cron Job or Option
      return list.sort((a, b) => {
        if (a.hasOwnProperty('cron_name')) {
          return sortByString(a.cron_name.toUpperCase(), b.cron_name.toUpperCase(), sort);
        } else if (a.hasOwnProperty('option_name')) {
          return sortByString(a.option_name.toUpperCase(), b.option_name.toUpperCase(), sort);
        }
        return 0;
      })

    case "autoloaded":
      return list.sort((a, b) => sortByString(b.autoload, a.autoload, sort));
  }
  return [...list];
}
// Filters
const filterItems = (list, filterBy, filtersDef, hideUsedByWordPress, search) => {
  let targetList = [...list];

  if (hideUsedByWordPress) {
    targetList = filterWithUsedBy([...targetList], 'wordpress');
  }

  if (search) {
    targetList = filterWithSearchText([...targetList], search);
  }

  const filterValue = filtersDef.find(v => v.value === filterBy)?.filterValue ?? '';
  if (!filterValue) return [...targetList];
  if (['yes', 'no'].includes(filterValue)) {
    return targetList.filter(v => v.autoload === filterValue);
  }
  return targetList.filter(v => v.info?.status === filterValue);
}

const filterWithUsedBy = (list, value) => {
  return list.filter(v => v.info?.usedBy.toLowerCase() !== value);
}

const filterWithSearchText = (list, value) => {
  return list.filter(v => {
    // Cron Jobs
    if (v.hasOwnProperty('cron_name') && v.cron_name.includes(value)) {
      return true;
    }
    // Tables
    if (v.hasOwnProperty('table') && v.table.includes(value)) {
      return true;
    }
    // Post Types
    if (v.hasOwnProperty('name') && v.name.includes(value)) {
      return true;
    }
    // Option Names
    if (v.hasOwnProperty('option_name') && v.option_name.includes(value)) {
      return true;
    }
    return v.info.usedBy.toLowerCase().includes(value.toLowerCase());
  });
}

// Load items
const coreKeys = Object.keys(coreCount).map(k => snakeToCamel(k));
const findKey = (obj, item) => Object.keys(obj).find(k => obj[k].some(v => v.item === item));
const findInList = (list, key) => list?.find(v => typeof v.count !== 'undefined' && Number(v.count) > 0)?.[key] ?? null;
const findNextOrPrev = (list, key, value, nextOrPrev) => {
  const currentIndex = list.findIndex(v => v[key] === value);
  const haystack = (currentIndex === -1) ? [...list] : [...list.slice(currentIndex + 1), ...list.slice(0, currentIndex)];
  if (nextOrPrev === 'prev') {
    haystack.reverse();
  }
  return findInList(haystack, key);
}
const getFirstLoadEntryItem = (currentItem, loadEntryItems) => {
  const currentItemKey = findKey(loadEntryItems, currentItem);
  if (!coreKeys.includes(currentItemKey)) {
    return null;
  }
  return findInList(loadEntryItems[coreKeys.at(0)], 'item');
}
const getLastLoadEntryItem = (currentItem, loadEntryItems) => {
  const currentItemKey = findKey(loadEntryItems, currentItem);
  if (!coreKeys.includes(currentItemKey)) {
    return null;
  }
  const list = loadEntryItems[coreKeys.at(-1)];
  list.reverse();
  return findInList(list, 'item');
}

const makeLoadingItemKeys = (key, nextOrPrev) => {
  const currentIndex = coreKeys.findIndex(k => k === key);
  const newLoadingItemKeys = [...coreKeys.slice(currentIndex), ...coreKeys.slice(0, currentIndex)];
  if (nextOrPrev === 'prev') {
    newLoadingItemKeys.reverse();
  }
  return newLoadingItemKeys;
}

const getNextOrPrevLoadEntryItem = (currentItem, loadEntryItems, nextOrPrev = 'next') => {
  const currentItemKey = findKey(loadEntryItems, currentItem);
  if (!coreKeys.includes(currentItemKey)) {
    return findNextOrPrev([...loadEntryItems[currentItemKey]], 'item', currentItem, nextOrPrev);
  }
  const keys = makeLoadingItemKeys(currentItemKey, nextOrPrev);
  let returnItem = undefined;
  keys.some(key => {
    const item = findNextOrPrev([...loadEntryItems[key]], 'item', currentItem, nextOrPrev);
    if (item) {
      returnItem = item;
    }
    return !!item;
  });
  return returnItem;
}

